/// <reference path="../../typings/main.d.ts" />
var Console = require('./console/index');
//import RamlScriptReport = require('./ramlscript/reportUI');
var apiList = require('./raml1/popularApis');
var editorTools = require('./raml1/editorTools');
var quickCommands = require('./util/quick-commands');
var provider = require("./raml1/provider");
var quickOutline = require("./raml1/quickOutline");
var decl = require("./raml1/assistUtils");
var linterUI = require("./raml1/linterUI");
//import to=require("./raml1/tooltipManager")
var CompositeDisposable = require('atom').CompositeDisposable;
var commandManager = require("./raml1/commandManager");
var contextMenu = require("./raml1/contextMenu");
var commonContextActions = require("./raml1/commonContextActions");
var quickFixActions = require("./raml1/quickFix");
var package_entry_point;
(function (package_entry_point) {
    var subscriptions = new CompositeDisposable();
    function activate(state) {
        require('atom-package-deps').install('api-workbench', true)
            .then(function () {
            subscriptions.add(atom.commands.add('atom-workspace', {
                'api-workbench:popular-apis': apiList.showPopularApis,
                'api-workbench:editor-tools': editorTools.initEditorTools,
                'api-workbench:console': Console.toggle,
                'api-workbench:go-to-definition': decl.gotoDeclaration,
                'api-workbench:find-usages': decl.findUsages,
                'api-workbench:quick-outline': quickOutline.show,
                'api-workbench:quick-commands': quickCommands.showCommands,
                'api-workbench:rename': decl.renameRAMLElement,
                'api-workbench:new-project': decl.newProject,
                'api-workbench:select-node': decl.select
            }));
            subscriptions.add(atom.workspace.addOpener(Console.opener));
            //subscriptions.add(atom.workspace.addOpener(RamlScriptReport.opener))
            commandManager.initialize();
            contextMenu.initialize();
            commonContextActions.initialize();
            quickCommands.registerCommands();
            quickFixActions.initialize();
            editorTools.initEditorTools();
        });
    }
    package_entry_point.activate = activate;
    function getProvider() {
        return provider;
    }
    package_entry_point.getProvider = getProvider;
    function provideLinter() {
        return linterUI;
    }
    package_entry_point.provideLinter = provideLinter;
    function consumeLinter(linterApi) {
        subscriptions.add(linterUI.initEditorObservers(linterApi));
    }
    package_entry_point.consumeLinter = consumeLinter;
    function deactivate() {
        subscriptions.dispose();
    }
    package_entry_point.deactivate = deactivate;
    package_entry_point.config = {
        grammars: {
            type: 'array',
            default: [
                'source.raml'
            ]
        },
        openConsoleInSplitPane: {
            type: 'boolean',
            default: true
        }
    };
})(package_entry_point || (package_entry_point = {}));
module.exports = package_entry_point;




